import { Component, OnInit } from '@angular/core';
import * as Bcryptjs from "bcryptjs";
import { MethodApiServiceService } from '../../services/method-api-service.service';
import { NavController } from '@ionic/angular';
@Component({
  selector: 'app-registrar',
  templateUrl: './registrar.page.html',
  styleUrls: ['./registrar.page.scss'],
})
export class RegistrarPage implements OnInit {

  //Variables del formulario
  name:string;
  celular:number;
  cedula:number;
  email:string;
  password:string;

  verifyStatus=null;
  //fin variables

  constructor(private _methodsApiRestService: MethodApiServiceService,
    public navCtrl: NavController) { }

  ngOnInit() {
  }

  sendRegistro(){
    var salt = Bcryptjs.genSaltSync(10);
    var hash = Bcryptjs.hashSync(this.password, salt);
    let datos={
      "name":this.name,
      "celular":this.celular,
      "cedula":this.cedula,
      "email":this.email,
      "password":hash
    }
    this._methodsApiRestService.PostMethod('/users/create',datos)
    .subscribe(
      response => {
        if(response) {
          localStorage.setItem('name', this.name);
          localStorage.setItem('email', this.email);
          localStorage.setItem('cedula', this.cedula.toString());
          localStorage.setItem('celular', this.celular.toString());
          this.navCtrl.navigateRoot('/inicio');
        }else{
          
        }
      },
        error => {
          //console.log(error);
          if (!error.ok) {
            
          }
        }
    );
  }


}
